const getIdByRoute = () => {
    let pathInfo = location.pathname.split('/')
        .filter((item: string) => {
            if (item) {
                return true;
            }
            return false;
        });
    return pathInfo[pathInfo.length-1];
}

interface IUrlParamInfo {
    keys: Array<string>,
    params: {
        [key: string]: string|Array<string>
    },
    get:(key:string)=> string|Array<string>,
}

const pickUpUrlParams = () => {
    let url = location.search; //获取url中"?"符后的字串 

    let urlParamInfo: IUrlParamInfo = {
        keys: [],
        params: {},
        get: (key: string) => {
            return "";
        }
    };

    if (url.indexOf("?") !== -1) {
        let str = url.substr(1);
        let requestPairArr = str.split("&");
        let keyArr: Array<string> = [];
        let params: {
            [key: string]: string|Array<string>
        } = {};
        for (var i = 0; i < requestPairArr.length; i++) {
            let paramPair = requestPairArr[i].split("=");
            let key = paramPair[0].toLocaleLowerCase();
            if( params[key] ){
                params[key] = [ (<string>params[key]) ,unescape(paramPair[1])]
            }else{
                params[key] = unescape(paramPair[1]);
            }
            
            keyArr.push(key);
        }
        const get = (key: string) => {
            let lowKey = key.toLocaleLowerCase();
            if (keyArr.includes(lowKey)) {
                return params[key];
            } else {
                return "";
            }
        }
        urlParamInfo.keys = keyArr;
        urlParamInfo.params = params;
        urlParamInfo.get = get;
    }
    return urlParamInfo;
}


interface Iutil {
    "getIdByRoute": () => string,
    "pickUpUrlParams": () => IUrlParamInfo,
}
const util: Iutil = {
    "getIdByRoute": getIdByRoute,
    "pickUpUrlParams": pickUpUrlParams
};


export default util;