import "../../scss/pages/trivial-index.scss";
import Paging from "../../../common/Paging";
import "../../../common/extensions/global";
import util from "../../../common/util";
import Global from "../common/global";


(() => {
    let trivialListApi = "/api/Trivial/Index";
    let paging = new Paging("#paginationContainer", (page) => {
        window.location.href = "/Trivial/Index?page=" + page;
    });

    const parseData = (data: Array<any>) => {
        let html = data.map((item: any) => {
            let liStr = `<li>
                    <div class="date col-lg-3 col-md-3 col-sm-3 col-12">
                        <span>${item.Create.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss")}</span>
                    </div>
                    <div class="main col-lg-9 col-md-9 col-sm-9 col-12">
                        ${item.Content}
                    </div>
                </li>`;
            return liStr;
        }).join(" ");
        $("#trivialMain .timeline").html(html);
    }

    function getTrivial(page: number = 1) {
        let data: any = {
            page: page,
        };
        Global.showLoading();
        $.ajax({
            type: "get",
            url: trivialListApi,
            data: data,
            success: (msg: any) => {
                var pagingData = msg.Data;
                parseData(pagingData.Data);
                paging.createPagingLi(pagingData.PageIndex, pagingData.TotalPages);
            },
            complete: () => {
                Global.hideLoading();
            }
        });
    }

    let page = <string>util.pickUpUrlParams().get("page");
    if (!page) {
        getTrivial();
    } else {
        getTrivial(parseInt(page));
    }
})();